<template>
    <div class="artist-page">

        <!--TODO : make this an actual call, not this bullshit -->
        <!--  {{artists}}-->
        <div class="container">
            <div class="content-block">
                <router-link to="/artists" class="button">Back</router-link>
                <div class="title">{{pageArtist.data.artist_name[0].text}}</div>
                <img :src="`${pageArtist.data.artist_picture.url}`" :alt="`${pageArtist.data.artist_name[0].text}`"><br>
                <p v-for="line in pageArtist.data.artist_bio">
                    {{line.text}}
                </p>
            </div>
        </div>
        <hr>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Patterns from './patterns/AllPatterns'
    import ColorFilter from './filters/ColorFilter'
    import PatternFilter from './filters/PatternFilter'
    import AppliedFilters from './filters/AppliedFilters'
    import FilteredPatterns from './patterns/FilteredPatterns'

export default {
    components: {
      Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
    },
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  computed: {
      ...mapGetters({
          pageArtist: 'getPageArtistFromStore'
      })
  },
  methods: {
      ...mapActions({
          getPageArtist: 'getPageArtist'
      })
  },
  mounted() {
    this.getPageArtist(this.$route.params.artist);
  }
/*  components : {
    TODO: Add components
  }*/
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1, h2 {
        font-weight: normal;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    li {
        display: inline-block;
        margin: 0 10px;
    }

</style>


