<template>
  <div class="add-to-cart">
    <div v-on:click="addToCartHere(pattern)" class="big button" :id="`add-to-cart-${pattern.sku}`">
      ADD TO CART
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
      props: [
          'pattern'
      ],
    methods: {
      ...mapActions({
        addToCartHere: 'addToCart',
      })
    }
  }
</script>



