<template>
  <div class="favorites">
    <template v-for="favorite in favorites">
      <wallpaper-pattern v-bind:data="favorite"></wallpaper-pattern>
    </template>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import WallpaperPattern from '@/components/patterns/WallpaperPattern'

  export default {
    components : {
      WallpaperPattern
    },
    computed: {
      ...mapGetters({
        favorites: 'getFavoritesFromStore'
      })
    },
    methods: {
      ...mapActions({
          getFavoritesHere: 'getFavorites'
      })
    },
    mounted () {
      this.getFavoritesHere()
    }
  }
</script>




