<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    div
    <applied-filters></applied-filters>
    <pattern-filter></pattern-filter>
    <color-filter></color-filter>
    <h2>filtered patterns</h2>
    <filtered-patterns></filtered-patterns>
    <patterns></patterns>
  </div>
</template>

<script>
import Patterns from './patterns/AllPatterns'
import ColorFilter from './filters/ColorFilter'
import PatternFilter from './filters/PatternFilter'
import AppliedFilters from './filters/AppliedFilters'
import FilteredPatterns from './patterns/FilteredPatterns'

export default {
    components: {
      Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
    },
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
/*  components : {
    TODO: Add components
  }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



