<template>
    <header>
        <div class="header-cta" id="header-cta-banner">
<!--TODO : MAKE THIS PRISMIC-->
            {{getSample}}

        </div>
        <div class="nav">
        <span>
            <!-- TODO : logo? -->
            <!--
            EVOLUTION
            WALLPAPER
            -->
            <div class="hamburger" v-on:click="toggleMenu" id="hamburger-icon">
                <div class="bar" :class="{animate : getShowMenu}"></div>
            </div>
            
            <span @click="closeMenu" class="nav-header-text"><router-link to="/" id="desktop-logo">EVOLUTION  WALLPAPER</router-link></span>
            <span @click="closeMenu" class="nav-header-text-mobile"><router-link to="/" id="mobile-logo">EW</router-link></span>
            
            
        </span>
            <!-- TODO : make favorites and cart reflect number of favorites & cart total-->
            <span class="header-right" @click="closeMenu">
                <!-- :class="{underlined : getFavoritesCount}"-->
            <router-link to="/favorites" id="favorites-header-icon"><span class="favorites-count">{{ getFavoritesCount }}</span> <span class="header-heart"></span></router-link>
            <router-link to="/cart" id="cart-header-icon"><span class="cart-count"> cart {{ getCartCount }}</span> </router-link>
        </span>
        </div>
        <transition name="fade">
            <div class="fullsize-menu" v-show="getShowMenu">
                <ul>
                    <li><span @click="closeMenu"><router-link to="/"> home </router-link></span></li>
                    <li><span @click="closeMenu"><router-link to="/artists"> artists </router-link></span></li>
                    <li><span @click="closeMenu"><router-link to="/blog"> blog </router-link></span></li>
                    <li><span @click="closeMenu"><router-link to="/education"> education </router-link></span></li>
                    <li><span @click="closeMenu"><router-link to="/filter-search"> filter search </router-link></span></li>
                </ul>
            </div>
        </transition>
    </header>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        computed: {
            ...mapGetters([
                "getSample",
                "getShowMenu",
                "getFavoritesCount",
                "getCartCount"
                //this syntax maps directly to the store, instead of assigning like the tutorial example
            ])
        },
        methods: {
            ...mapActions({
                toggleMenu: 'toggleMenu',
                getFavoritesHere: 'getFavorites',
                getCartHere: 'getCart',
                closeMenu: 'closeMenu'
            })
        },
        mounted() {
            this.getFavoritesHere()
            this.getCartHere()
            //TODO : IS THERE AN EASIER/ NO API CALL WAY TO DO THIS
        }
    }
</script>




