<template>
  <div class="add-to-favorites">
    <div v-on:click="addToFavoritesHere(pattern)" class="heart" v-bind:class="{added : pattern.favorite}" :id="`add-to-favorites-${pattern.sku}`">
      
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
      props: [
          'pattern'
      ],
    methods: {
      ...mapActions({
        addToFavoritesHere: 'addToFavorites',
      })
    }
  }
</script>



