export const applyColorFilter = ({state}, color) => {
    color.active = !color.active;
    //TODO : get appliedPatternFilters
    state.appliedColorFilters = [];
    state.searchColorParams = '';

    for (let i = 0; i < state.colorFilters.length; i++) {
        if (state.colorFilters[i].active) {
            state.appliedColorFilters.push(state.colorFilters[i])
            state.searchColorParams += 'primaryColor=' + state.colorFilters[i].colorName + '&'

            // TODO : ITERATE OVER PATTERN FILTERS
        }
    }

    createParams(state,state.searchColorParams,state.searchPatternParams)
}

export const applyPatternFilter = ({state}, patternType) => {
    patternType.active = !patternType.active;

    //TODO : get appliedPatternFilters
    state.appliedPatternFilters = [];
    state.searchPatternParams = '';

    for (let i = 0; i < state.patternTypes.length; i++) {
        if (state.patternTypes[i].active) {
            state.appliedPatternFilters.push(state.patternTypes[i])
            state.searchPatternParams += 'patternType=' + state.patternTypes[i].name + '&'
        }
    }
    createParams(state,state.searchColorParams,state.searchPatternParams)
}

export const createParams = (state, colors, patterns) => { 
    var params = '?' + colors + patterns
    api.getFilteredPatterns(params).then((response) => {
        state.patterns = response.data
        console.log('RESPONSE IN ACTIONS = ', response.data)
    });
}
