export const removeFromCart = ({commit}, pattern) => {
    api.removeFromCart(pattern).then(function() {
        api.getCart().then(function(response) {
            commit('setCart', response.data)
        }, function(err) {
            console.log("Something went wrong: ", err);
        });
    })
    
}

