<template>
    <div class="cart-items">
        <div v-if="!cart.length">
            <div class="title">Your Cart Is Empty :(</div>
            <router-link class="big button" to="/filter-search">Start Shopping!</router-link>
        </div>
        <div v-for="item in cart" class="cart-item">
                <router-link class="cart-image-wrap" :to="`/pdp/${item.patternName}`"><img :src="`${item.img}`" :alt="`${item.patternName}`" class="cart-image"></router-link>
                <router-link class="no-style" :to="`/pdp/${item.patternName}`"><div class="pattern-name title">{{item.patternName}}</div></router-link>
                <span class="cart-item-price">${{item.prices.vinyl}}</span>
                <div class="subtext">{{item.patternDescription.short}}</div>
                <div class="button-wrap">
                    <div class="tiny plain red button" @click="removeFromCart(item)">Remove</div>
                </div>
                
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import WallpaperPattern from '@/components/patterns/WallpaperPattern'

    export default {
        components : {
            WallpaperPattern
        },
        computed: {
            ...mapGetters({
                cart: 'getCartFromStore'
            })
        },
        methods: {
            ...mapActions({
                getCartHere: 'getCart',
                removeFromCart: 'removeFromCart'
            })
        },
        mounted () {
            this.getCartHere()
        }
    }
</script>




