<template>
  <div class="pattern-filters">
    <p v-for="patternType in patternTypes" v-on:click="applyPatternFilter(patternType)" v-bind:class="{activePatternFilter : patternType.active}" class="pattern-filter-option" :id="`pattern-filter-${patternType.url_name}`">
      {{patternType.name}}
    </p>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
    /*    components : {
     WallpaperPattern
     },*/
    computed: {
      ...mapGetters({
        patternTypes: 'getPatternTypesFromStore'
      })
    },
    methods: {
      ...mapActions({
        getPatternTypesHere: 'getPatternTypes',
        applyPatternFilter: 'applyPatternFilter'
      })
    },
    mounted () {
      this.getPatternTypesHere()
    }
  }
</script>



