<template>
    <div id="app">
        <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=0"/>
        <WPHeader></WPHeader>
        <div id="router-container">
            <router-view/>
        </div>
        <WPFooter></WPFooter>
    </div>
</template>

<script>
    import WPHeader from './components/WPHeader'
    import WPFooter from './components/WPFooter'

    export default {
        name: 'App',
        components: {
            WPHeader, WPFooter
        }
    }
</script>

<style lang="scss">
    @import url('https://fonts.googleapis.com/css?family=Work+Sans|Playfair+Display:400,700');

    /*
    START VARIABLES
    */
    // fonts :
    $header-font: 'Playfair Display', serif;
    // 400 and 700 weights
    $content-font: 'Work Sans', sans-serif;
    ;
    // 300 weight
    $color1: rgba(60, 73, 63, 1);
    $color2: rgba(238, 99, 82, 1);
    $color3: rgba(5, 5, 23, 1);
    $color4: rgba(240, 247, 244, 1);
    $color5: rgba(239, 210, 203, 1);

    $body-background-color: rgba(238, 229, 233, 1);

    $content-background-color: #FFFDF5;
    $content-text-color: #383836;
    $content-accent-color: rgba(240, 243, 245, 1);
    $content-subtext-color: #7e7e7e;

    //Filter search
    $filter-width: 20%;
    $results-width: 100% - 20%;

    $highlight-color: #702626;

    $footer-cta-background-color: #af7056;
    $footer-cta-color: white;

    $header-cta-height: 30px;
    $nav-height: 60px;

    $top-level-border: 15px solid $body-background-color;

    $mobile-border: none;

    $header-height: $header-cta-height + $nav-height + 15px;
    $header-height-mobile: $header-cta-height + $nav-height;

    // TODO : WHY DOES CALC NOT PLAY NICE WITH Vars?
    $nav-menu-height: calc(100vh - 94px);

    //Hamburger variables
    $hamburger-margin-top: 18px;
    $bar-width: 45px;
    $bar-height: 2px;
    $bar-spacing: 10px;

    $hamburger-margin-top-mobile: 20px;
    $bar-width-mobile: 35px;
    $bar-height-mobile: 2px;
    $bar-spacing-mobile: 8px;

    $corner-radius: .5%;
    $parent-margin-size: 15px;
    $parent-border: 2px solid $content-accent-color;

    //color swatches
    $swatch-outline-width: 1.5px;
    $swatch-outline-offset: 2px;
    $swatch-margin: 7.5px;

    //media queries
    $xs: 600px;
    $s: 800px;
    $m: 1200px;
    $l: 1500px;
    $row2: 1297px;
    $row3: 656px;

    /*
    END VARIABLES
    */

    /*
    START MIXINS
    */
    @mixin high-res {
        @media (-webkit-min-device-pixel-ratio: 2) {
            @content;
        }
    }

    @mixin under-m {
        @media (max-width: #{$m}) {
            @content;
        }
    }

    @mixin under-s {
        @media (max-width: #{$s}) {
            @content;
        }
    }

    @mixin under-l {
        @media (min-width: #{$m}) and (max-width: #{$l}) {
            @content;
        }
    }

    @mixin over-s {
        @media (min-width: #{$s}) {
            @content;
        }
    }

    @mixin over-m {
        @media (min-width: #{$m}) {
            @content;
        }
    }

    @mixin xs {
        @media (max-width: #{$xs}) {
            @content;
        }
    }

    @mixin s {
        @media (min-width: #{$xs}) and (max-width: #{$s - 1px}) {
            @content;
        }
    }

    @mixin m {
        @media (min-width: #{$s}) and (max-width: #{$m - 1px}) {
            @content;
        }
    }

    @mixin l {
        @media (min-width: #{$m}) and (max-width: #{$l - 1px}) {
            @content;
        }
    }

    @mixin xl {
        @media (min-width: #{$l}) {
            @content;
        }
    }
    $home-height: calc(100vh - (#{$header-height} + 40px));
    $container-margin: 5px 15px;

    /*
   END MIXINS
   */

    /*
 START GLOBAL STYLES
 */

    [v-cloak] {
        display: none;
    }

    html {
        background-color: $body-background-color;
    }

    body {

        margin: 0;
        background-color: $body-background-color;
        font-family: $content-font;
        font-weight: 300;
    }
    #router-container{
        min-height: $home-height;
    }
    .container {
        margin: $container-margin;
        @include xs {
            margin: 5px 10px;
        }
        .content-block {
            border: $parent-border;
            margin-bottom: 15px;
            padding: 10px 20px;
            background-color: $content-background-color;
        }
    }

    .centered {
        text-align: center;
    }

    .button-wrap {
        display: inline-block;
        width: 100%;
    }

    .button {
        display: inline-block;
        padding: 10px 20px;
        border-radius: 2px;
        text-shadow: 1px 1px 0 #555;
        background-color: $footer-cta-background-color;
        color: $footer-cta-color;
        transition: all 0.5s ease;
        cursor: pointer;
        text-decoration: none;
        text-transform: uppercase;
        &:hover {
            background-color: darken($footer-cta-background-color, 10%);
        }
        &.plain {
            background-color: transparent;
            color: $content-text-color;
            text-shadow: none;
            border: 1px solid #000;
            &:hover {
                background-color: transparent;
            }
        }
        &.big {
            padding: 15px 40px;
            font-size: 1.5em;
        }
        &.small {
            padding: 5px 18px;
            font-size: .8em;
        }
        &.tiny {
            padding: 3px 14px;
            font-size: .65em;
        }
        &.checkout {
            text-align: center;
            margin: 0 0 20px;
        }
        &.red {
            border: 1px solid red;
            color: red;
        }
    }

    .slide-right-enter-active {
        transition: all .3s ease;
    }

    .slide-right-leave-active {
        transition: all .3s ease;
    }

    .slide-right-enter, .slide-right-leave-to {
        transform: translateX(50px);
        opacity: 0;
    }

    #app {
        margin: 0;
        padding: ($header-height + 10px) 0 0 0;
        position: relative;
        width: 100%;
        @include xs {
            padding: ($header-height-mobile + 10px) 0 0 0;
        }
    }

    h1, h2, h3, h4, h5 {
        font-family: $header-font;
        font-weight: 300;
        text-transform: uppercase;
        &.no-margin-t {
            margin-top: 0;
        }
        &.no-margin-b {
            margin-bottom: 0;
        }
    }

    .title {
        //font-weight: 300;
        text-transform: uppercase;
        font-size: 1.5em;
        // margin-bottom: 1%;
        text-decoration: none;
    }

    .subheader {
        font-size: 1.2em;
        display: block;
        margin: 10px 0;
    }

    .subtext {
        font-size: .8em;
        display: inline-block;
        color: $content-subtext-color;
    }

    a {
        cursor: pointer;
        &.no-style {
            text-decoration: none;
            color: inherit;
        }
    }

    header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 99;

        height: $header-height;
        border: $top-level-border;
        box-sizing: border-box;
        /*    box-shadow: 0px 1px 8px 0px $content-text-color;*/
        @include xs {
            border: $mobile-border;
        }

        .nav-header-text {
            font-family: $header-font;
            font-size: 30px;
            @include under-s {
                font-size: 25px;
            }
            @include xs {
                display: none;
            }
        }
        .nav-header-text-mobile {
            font-family: $header-font;
            font-size: 30px;
            display: none;
            @include xs {
                display: inherit;
            }
        }
        .header-cta {
            height: $header-cta-height;
            line-height: $header-cta-height;
            text-align: center;
            background-color: $footer-cta-background-color;
            color: $footer-cta-color;
        }
        .nav {
            background-color: $content-background-color;
            color: $content-text-color;
            text-align: center;
            line-height: $nav-height;
            height: $nav-height;
            border: $parent-border;
            .header-right {
                position: absolute;
                right: 20px;

                .underlined {
                    border-bottom: 2px solid $highlight-color;
                }
            }
            .hamburger {
                width: $bar-width;
                height: $bar-height + $bar-spacing*2;
                cursor: pointer;
                position: absolute;
                left: 20px;
                margin-top: $hamburger-margin-top;
                @include xs {
                    width: $bar-width-mobile;
                    height: $bar-height-mobile + $bar-spacing-mobile*2;
                    margin-top: $hamburger-margin-top-mobile;
                }
            }
            .bar,
            .bar:after,
            .bar:before {
                width: $bar-width;
                height: $bar-height;
                @include xs {
                    width: $bar-width-mobile;
                    height: $bar-height-mobile;
                }
            }
            .bar {
                position: relative;
                transform: translateY($bar-spacing);
                transition: all 0ms 300ms;
                @include xs {
                    transform: translateY($bar-spacing-mobile);
                }

                &.animate {
                    background: rgba(255, 255, 255, 0);
                }
            }

            .bar:before {
                content: "";
                position: absolute;
                left: 0;
                bottom: $bar-spacing;
                background: $footer-cta-background-color;
                transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
                @include xs {
                    bottom: $bar-spacing-mobile;
                }
            }

            .bar:after {
                content: "";
                position: absolute;
                left: 0;
                top: $bar-spacing;
                background: $footer-cta-background-color;
                transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
                @include xs {
                    top: $bar-spacing-mobile;
                }
            }

            .bar.animate:after {
                top: 0;
                transform: rotate(45deg);
                transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
            }

            .bar.animate:before {
                bottom: 0;
                transform: rotate(-45deg);
                transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
            }
        }
        .evolution-wallpaper {
            font-family: $header-font;
            font-weight: 700;
            font-size: 30px;
            color: $highlight-color;
        }
        a {
            color: $content-text-color;
            text-decoration: none;
            padding: 5px;
        }

        //Transition styles for showing menu
        .fade-enter-active, .fade-leave-active {
            transition: opacity 1s;
        }
        .fade-enter, .fade-leave-to {
            opacity: 0;
        }

        .header-heart {
            color: red;
            font-size: 2em;
            @include under-s {
                font-size: 1.75em;
            }
            @include xs {
                font-size: 1.5em;
            }
        }
        .favorites-count {
            position: absolute;
            background-color: white;
            color: #383836;
            height: 20px;
            width: 20px;
            line-height: 20px;
            text-align: center;
            border: 1px solid red;
            left: 22px;
            top: 4px;
            border-radius: 25px;
            font-size: .8em;
            @include under-s {
                font-size: .75em;
                left: 20px;
                top: 6px;
            }
            @include xs {
                font-size: .65em;
                width: 18px;
                height: 18px;
                top: 8px;
                left: 18px;
            }
        }

        .fullsize-menu {
            width: 100%;
            height: $nav-menu-height;
            position: absolute;
            background-color: $content-background-color;
            display: flex;
            align-content: center;
            justify-content: center;
            transition: all 0.5s ease;
            border: $parent-border;
            box-sizing: border-box;
            font-family: $header-font;
            text-transform: uppercase;
            text-align: center;

            ul {
                list-style-type: none;
                padding: 20vh 0;
                display: flex;
                align-content: space-between;
                flex-direction: column;
                @include xs {
                    padding: 8vh 0;
                }

                li {
                    font-size: 2em;
                    flex-grow: 1;
                    @include xs {
                        font-size: 1.75em;
                    }

                    a {
                        &:hover {
                            color: $footer-cta-background-color;
                            -webkit-transition: color .5s;
                            transition: color .5s;
                        }
                    }
                }
            }
        }
    }

    footer {
        width: 100%;
        z-index: 99;
        background-color: $footer-cta-background-color;
        color: $footer-cta-color;
        border: $top-level-border;
        box-sizing: border-box;
        text-align: center;
        @include xs {
            border: $mobile-border;
        }
        .social-media-footer-icon {
            width: 75px;
        }
    }

    /*
 END GLOBAL STYLES
  */

    /*
START HOME
*/
    $homepage-border: 5px double $footer-cta-background-color;
    #home-container {
        margin: $container-margin;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        justify-content: space-between;
        @include under-m {
            flex-direction: column;
        }
        #home-main-content {
            border: $homepage-border;
            width: 59%;
            height:  $home-height;
            box-sizing: border-box;

            background-image: url("https://assets.fathead.com/evolution/homepage.jpg");
            background-size: cover;
            background-position: center;
            display: flex;
            align-items: center;
            justify-content: center;
            #home-main-cta{
                border: $homepage-border;
                background-color: $content-background-color;
                text-align: center;
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding: 25px;
                max-width: 80%;
            }
            img {
                width: 100%;
            }
            @include under-m {
                width: 100%;
                margin-bottom: 10px;
            }
        }
        #home-side-content {
            width: 40%;
            height: $home-height;
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            justify-content: space-between;
            @include under-m {
                width: 100%;
            }
            .side-item {
                height: 49.5%;
                border: $homepage-border;
                width: 100%;
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                background-color: $content-background-color;
                background-size: cover;
                background-position: center;
                @include under-m {
                    height: 50%;
                    margin-bottom: 10px;
                }
            }
        }
    }

    /*
END HOME
*/

    /*
 START WALLPAPER PATTERN
 */

    .wallpaper-pattern {
        word-wrap: break-word;
        border: 1px solid $content-accent-color;
        background-color: white;
        color: $content-text-color;
        display: flex;
        justify-content: center;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        a {
            text-decoration: none;
            color: $highlight-color;
        }
        h1, h2, h3, h4, h5 {
            padding: 0;
            margin: 0;
            text-align: center;
            background-color: $content-background-color;
        }
    }

    /*
 END WALLPAPER PATTERN
 */

    /*
 START FILTER SEARCH PAGE
 */

    .patterns {
        display: grid;
        grid-gap: 45px 10px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-auto-rows: 200px;
    }

    .filter-search {
        .filter-container {
            display: flex;
            flex-direction: row;
            box-sizing: border-box;
            position: relative;
            margin: $container-margin;
        }

        .filters {
            width: $filter-width;
            min-width: 300px;
            background-color: $content-background-color;
            color: $content-text-color;
            border: $parent-border;
            box-sizing: border-box;
            padding: 0 1%;
            margin-right: 15px;
            @include under-s {
                display: none;
            }
        }
        .filters-mobile {
            display: none;
            position: fixed;
            width: 70%;
            height: 100%;
            box-sizing: border-box;
            padding: 2% 5%;
            z-index: 99;
            background-color: $content-background-color;
            overflow: scroll;
            right: 0;
            top: 0;

            @include under-s {
                display: block;
            }
        }
        #mobile-filters-button {
            float: right;
            margin: 5px 0;
            @include over-s {
                display: none;
            }
        }
        .overlay {
            position: fixed;
            width: 100%;
            height: 100%;
            z-index: 99;
            top: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.5);
        }
        .results {
            width: $results-width;
            background-color: $content-background-color;
            color: $content-text-color;
            border: $parent-border;
            padding: 0 1%;
            box-sizing: border-box;
            @include under-s {
                width: 100%;
            }
        }
    }

    .activeFilter {
        outline: $swatch-outline-width solid $highlight-color;
        outline-offset: $swatch-outline-offset;
    }

    .activePatternFilter:before {

        content: 'X ';
        color: $highlight-color;

    }

    .activeFilterText {
        font-weight: 800;
    }

    .color-filters {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .pattern-filters {

    }

    .pattern-filter-option {
        cursor: pointer;
    }

    $swatch-size: 25px;
    .color-swatch {
        box-sizing: border-box;
        height: $swatch-size;
        width: $swatch-size;
        margin: $swatch-margin;
        border: 1px solid $content-accent-color;
    }

    /*
 END FILTER SEARCH
 */
    /*
 START FAVORITES
 */
    .favorites {
        display: grid;
        grid-gap: 45px 20px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-auto-rows: 200px;
        padding-bottom: 30px;
    }

    .add-to-favorites {
        position: absolute;
        right: 2px;
        top: 0px;

        .heart {
            color: red;
            cursor: pointer;
            font-size: 20px;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: white;
        }
        .added {
            color: black !important;
        }
    }

    /*
 END FAVORITES
 */
    /*
 START ARTISTS PAGE
 */
    .artists-page {
        p {
            display: inline-block;
            margin: 0 .5em;
            vertical-align: top;
        }
        img {
            max-width: 200px;
        }
        .artists-container {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            margin: 5px 15px;
            @include xs {
                margin: 5px 10px;
            }
            .artist-content-block {
                border: $parent-border;
                margin-bottom: 15px;
                padding: 10px 20px;
                background-color: $content-background-color;
                width: 200px;
                @include under-s {
                    width: 100%;
                    text-align: center;
                }
            }
        }

    }

    /*
 END ARTISTS PAGE
 */
    /*
 START ARTIST PAGE
 */
    .artist-page {
        img {
            max-width: 200px;
        }
    }

    /*
 END ARTIST PAGE
 */
    /*
 START EDUCATION PAGE
 */
    .education-page {
        img {
            max-width: 200px;
        }
    }

    /*
 END EDUCATION PAGE
 */
    /*
 START PDP PAGE
 */

    .pdp {
        .content-block {
            text-align: center;
        }
    }

    .pdp-wrap {
        display: inline-block;
        width: 80%;
        @include under-m {
            width: 100%;
        }
        .pdp-inner {
            text-align: center;
            display: flex;
            align-content: center;
            flex-direction: row;
            @include under-s {
                flex-direction: column;
            }
            .image-wrap {
                width: 55%;
                @include under-s {
                    width: 100%;
                }
            }
            .product-detail-wrap {
                width: 45%;
                text-align: left;
                @include under-s {
                    width: 80%;
                    align-self: center;
                }
                .add-to-cart {
                    .button {
                        width: 100%;
                        text-align: center;
                        padding-left: 0;
                        padding-right: 0;
                        max-width: 300px;
                        @include under-s {
                            max-width: 1000px;
                        }
                    }
                }
                .price {
                    font-size: 1.75em;
                    margin: 30px 0;
                }
                .material-picker {
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    max-width: 300px;
                    @include under-s {
                        max-width: 1000px;
                    }
                    .material-option {
                        width: 100px;
                        padding: 10px 20px;
                        text-align: center;
                        cursor: pointer;
                        line-height: 1.25em;
                        &.selected {
                            border-color: $highlight-color;
                            background-color: $highlight-color;
                            color: #fff;
                        }
                        @include under-s {
                            width: 48%;
                            padding: 10px 0;
                        }
                    }
                }
            }
            .pdp-image {
                width: 80%;
            }
        }
    }

    /*
 END PDP PAGE
 */
    /*
 START CART
 */
    .cart-page {
        .cart-container {
            display: flex;
            flex-direction: row;
            box-sizing: border-box;
            position: relative;
            @include under-s {
                flex-direction: column-reverse;
            }
        }
    }

    .cart-list {
        width: 70%;
        box-sizing: border-box;
        display: inline-block;
        @include under-m {
            width: 60%;
        }
        @include under-s {
            width: 100%;
        }
        .cart-list-inner {
            width: 90%;
            margin: auto;
        }
    }

    .cart-total {
        width: 30%;
        box-sizing: border-box;
        display: inline-block;
        margin-left: 15px;
        display: flex;
        flex-direction: column;
        @include under-m {
            width: 40%;
        }
        @include under-s {
            width: 100%;
            margin-left: 0;
        }
        .cart-line {
            display: flex;
            justify-content: space-between;
            padding: 20px 0;
        }
    }

    .cart-item {
        display: grid;
        grid-template-columns: 150px 1fr 20px;
        grid-template-rows: auto 1fr auto;
        margin-bottom: 50px;
        grid-column-gap: 20px;
        @include under-m {
            grid-template-rows: auto 2fr 1fr auto;
        }
        @include xs {
            grid-template-rows: auto auto auto auto auto;
            grid-template-columns: 1fr;
            grid-row-gap: 5px;
        }
        .cart-image-wrap {
            grid-area: 1 / 1 / -1 / 2;
            @include xs {
                grid-area: 1 / 1 / 2 / -1;
            }
            .cart-image {
                width: 150px;
                height: 150px;
                display: inline-block;
                @include xs {
                    width: 100%;
                    height: auto;
                }
            }
        }

        .pattern-name {
            grid-area: 1 / 2 / 2 / 3;
            @include xs {
                grid-area: 2 / 1 / 3 / -1;
            }
        }
        .subtext {
            grid-area: 2 / 2 / 3 / 3;
            @include xs {
                display: none;
            }
        }
        .button-wrap {
            grid-area: 3 / 2 / -1 / 3;
            @include under-m {
                grid-area: 4 / 2 / 5 / 3;
            }
            @include xs {
                grid-area: 5 / 1 / 6 / -1;
            }
        }
        .cart-item-price {
            grid-area: 1 / 3 / 2 / 4;
            @include under-m {
                grid-area: 3 / 2 / 4 / 3;
            }
            @include xs {
                grid-area: 4 / 1 / 5 / -1;
            }
        }
    }

