<template>
    <div class="wallpaper-pattern">
        <add-favorite v-bind:pattern="data"></add-favorite>

        <router-link :to="`/pdp/${data.patternName}`" :id="`pattern-link-${data.sku}`">
            <img v-bind:src="data.img">
            <h3>{{data.patternName}}</h3>

           <!-- {{data}}-->
        </router-link>
    </div>
</template>

<script>
import AddFavorite from '@/components/favorites/AddFavorite'

    export default {
        props: [
            'data'
        ],
        components : {
            AddFavorite
        }
    }
</script>



