<template>
  <div class="patterns">
    <div @click="getBlog()"><h1>GET BLOG</h1></div>

    <wallpaper-pattern v-for="pattern in patterns"
                        :key="pattern.id"
                        v-bind:data="pattern"></wallpaper-pattern>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import WallpaperPattern from './WallpaperPattern'

  export default {
      components : {
          WallpaperPattern
      },
      computed: {
      ...mapGetters({
        patterns: 'getPatternsFromStore',
        filters: 'getFiltersFromStore'
      })
    },
    methods: {
      ...mapActions({
        getPatternsHere: 'getPatterns',
        getBlog: 'getBlog'

      })
    },
    mounted () {
      this.getPatternsHere()
    }
  }
</script>

<style scoped lang="scss">
  img{
    width: 50px;
    height: 50px;
  }
</style>



