<template>
  <div class="blog-post">
    <div class="container">
      <div class="content-block">
        <router-link to="/blog" class="button">back </router-link>
        <h1>{{post.data.title[0].text}}</h1>
        <div class="subheader">{{post.data.subheader[0].text}}</div>
        <span class="subtext">By {{post.data.author[0].text}} on {{post.data.article_date}}</span>
        <p v-for="paragraph in post.data.blog_content" >
          {{paragraph.text}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import Patterns from './patterns/AllPatterns'
import ColorFilter from './filters/ColorFilter'
import PatternFilter from './filters/PatternFilter'
import AppliedFilters from './filters/AppliedFilters'
import FilteredPatterns from './patterns/FilteredPatterns'

export default {
  components: {
    Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
  },
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  computed: {
        ...mapGetters({
            post: 'getBlogPostFromStore',
        })
    },
  methods: {
      ...mapActions({
          getBlogPostHere: 'getBlogPost',
      })
  },
  mounted : function (){
      this.getBlogPostHere(this.$route.params.post);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

</style>



