<template>
  <div id="swing-finder">

    <div v-for="(pattern, index) in patterns" class="box-container">
    <vue-swing
            @throwoutleft="throwout();"
            @throwoutright="throwin(); "
            :config="config"
    >
      <div >
      <div class="box" v-bind:style="{ 'background-image' : 'url('+pattern.img+')' }">{{pattern}}</div>
      </div>
    </vue-swing>

    </div>
  </div>
</template>

<script>
    import VueSwing from 'vue-swing'
    import {mapGetters, mapActions} from 'vuex'


export default {
    components: {
      VueSwing
    },
  name: 'SwipeFinder',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
        config: {

        },
        throwout: function () {
        console.log('throw it out' )
            let cardHolder = document.getElementById("swing-finder");
            let cards = document.getElementsByClassName('box-container');
        console.log('cards = ', cards)
            cardHolder.removeChild(cardHolder.childNodes["0"]);
            // this.patterns.splice(this.index, 1)
        },
        throwin: function (pattern) {
          console.log('throw it in')
        }
    }
  },
    computed: {
        ...mapGetters({
            patterns: 'getPatternsFromStore'
        })
    },
    methods: {
        ...mapActions({
            getPatternsHere: 'getPatterns'
        })
    },
    beforeMount () {
        this.getPatternsHere()
    },
    mounted () {

    }
}
</script>

<style lang="scss">
  .box{
    width: 50%;
    height: 500px;
    background-color: white;
    position: absolute;
    background-repeat: repeat;
    margin-left: 25%;
  }
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->




