<template>
  <div class="color-filters">
    <div v-for="color in colors" v-on:click="applyColorFilter(color)" :id="`color-filter-${color.colorName}`">
      <div class="color-swatch" v-bind:style="{ 'background-color': color.colorName }" v-bind:class="{activeFilter : color.active}"></div>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    computed: {
      ...mapGetters({
        colors: 'getColorFiltersFromStore'
      })
    },
    methods: {
      ...mapActions({
        getColorFiltersHere: 'getColorFilters',
        applyColorFilter: 'applyColorFilter'
      })
    },
    mounted () {
      this.getColorFiltersHere()
    }
  }
</script>



