<template>
  <div class="blog">
    <!-- TODO : Blog Params?? -->
    <div class="container">
      <div class="content-block" v-for="blog in blogs">
        <router-link :to="`/blog/${blog.uid}`"><div class="title">{{blog.data.title[0].text}}</div></router-link>
        <div class="subheader">{{blog.data.subheader[0].text}}</div>
        <span class="subtext">By {{blog.data.author[0].text}} on {{blog.data.article_date}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import Patterns from './patterns/AllPatterns'
import ColorFilter from './filters/ColorFilter'
import PatternFilter from './filters/PatternFilter'
import AppliedFilters from './filters/AppliedFilters'
import FilteredPatterns from './patterns/FilteredPatterns'

export default {
    components: {
      Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
    },
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  computed: {
    ...mapGetters({
      blogs: 'getBlogsFromStore',
    })
  },
  methods: {
    ...mapActions({
      getBlogsHere: 'getBlogs',
    })
  },
  mounted() {
    this.getBlogsHere()
  }
/*  components : {
    TODO: Add components
  }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



