<template>
  <div class="artists-page">
    <div class="artists-container">
      <div v-for="artist in artists" class="artist-content-block">
        <router-link :to="`/artist/${artist.uid}`"><div class="title">{{artist.data.artist_name[0].text}} </div></router-link>
        <router-link :to="`/artist/${artist.uid}`"><img :src="`${artist.data.artist_picture.url}`"></router-link>
        <p>{{artist.artist_bio}}</p>
        <!--{{artist}}-->
      </div>
    </div>
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Patterns from './patterns/AllPatterns'
    import ColorFilter from './filters/ColorFilter'
    import PatternFilter from './filters/PatternFilter'
    import AppliedFilters from './filters/AppliedFilters'
    import FilteredPatterns from './patterns/FilteredPatterns'

    export default {
        components: {
            Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
        },
        name: 'HelloWorld',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed: {
            ...mapGetters({
                artists: 'getArtistsFromStore',
            })
        },
        methods: {
            ...mapActions({
                getArtistsHere: 'getArtists',
            })
        },
        mounted : function (){
            this.getArtistsHere();
        }
      /*  components : {
       TODO: Add components
       }*/
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>


