<template>
  <div class="pdp">
    <div class="container">
      <div class="content-block">
        <div class="pdp-wrap">
          <div class="pdp-inner">
            <div class="image-wrap">
              <img :src="`${patternInfo.img}`" alt="" v-cloak class="pdp-image">
            </div>
            <div class="product-detail-wrap">
              <h1 class="no-margin-t">{{patternInfo.patternName}}</h1>
              <div class="material-picker">
                <!-- TODO : make this a component, not an inline item -->
                <div class="material-option plain button" :class="{ selected : materialSelected == material }" v-for="material in materials" @click="selectMaterial(material)">{{material.name}}<br>${{material.price}}</div>
              </div>
              <div class="price">${{price}}</div>
              <add-to-cart v-bind:pattern="patternInfo">
              </add-to-cart>
              <h3>{{patternInfo.patternType}}, {{patternInfo.primaryColor}}   </h3>
              {{patternInfo.patternDescription.long}}
                          <h1> SOCIAL SHARING NEEDS TO BE CONFIGURED ONCE WE HAVE A SERVER AND THE SELECTED ITEM NEEDS TO BE A COMPONENT</h1>
              <h2>Vue GoodShare</h2>
              <vue-goodshare></vue-goodshare>
              <h2>Vue Social Sharing (more customizable and more options)</h2>
              <social-sharing url="https://vuejs.org/"
                              title="The Progressive JavaScript Framework"
                              description="Intuitive, Fast and Composable MVVM for building interactive interfaces."
                              quote="Vue is a progressive framework for building user interfaces."
                              hashtags="vuejs,javascript,framework"
                              twitter-user="vuejs"
                              inline-template>
                <div>
                  <network network="email">
                    <i class="fa fa-envelope"></i> Email
                  </network>
                  <network network="facebook">
                    <i class="fa fa-facebook"></i> Facebook
                  </network>
                  <network network="googleplus">
                    <i class="fa fa-google-plus"></i> Google +
                  </network>
                  <network network="line">
                    <i class="fa fa-line"></i> Line
                  </network>
                  <network network="linkedin">
                    <i class="fa fa-linkedin"></i> LinkedIn
                  </network>
                  <network network="odnoklassniki">
                    <i class="fa fa-odnoklassniki"></i> Odnoklassniki
                  </network>
                  <network network="pinterest">
                    <i class="fa fa-pinterest"></i> Pinterest
                  </network>
                  <network network="reddit">
                    <i class="fa fa-reddit"></i> Reddit
                  </network>
                  <network network="skype">
                    <i class="fa fa-skype"></i> Skype
                  </network>
                  <network network="sms">
                    <i class="fa fa-commenting-o"></i> SMS
                  </network>
                  <network network="telegram">
                    <i class="fa fa-telegram"></i> Telegram
                  </network>
                  <network network="twitter">
                    <i class="fa fa-twitter"></i> Twitter
                  </network>
                  <network network="vk">
                    <i class="fa fa-vk"></i> VKontakte
                  </network>
                  <network network="weibo">
                    <i class="fa fa-weibo"></i> Weibo
                  </network>
                  <network network="whatsapp">
                    <i class="fa fa-whatsapp"></i> Whatsapp
                  </network>
                </div>
              </social-sharing>
            </div>
          </div>
        </div>
      </div>
    </div>
    

    <!--    {{patternInfo}}
        {{patternInfo}}
        {{patternInfo}}
        {{patternInfo}}-->
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import VueGoodshare from 'vue-goodshare'
    import SocialSharing from 'vue-social-sharing'
    import AddToCart from '@/components/cart/AddToCart'



export default {
  name: 'PDP',
    components : {
        VueGoodshare, SocialSharing, AddToCart
    },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      materialSelected: null,
      price: null
    }
  },
    computed: {
        ...mapGetters({
            //TODO : make Pattern Info Grabber
            patternInfo: 'getPatternInfoFromStore',
            materials: 'getMaterialsFromStore'
        })
    },
    methods: {
        selectMaterial(material) {
          this.materialSelected = material
          this.price = material.price
        },
        ...mapActions({
            getPatternInfo: 'getPatternInfo',
            getMaterials: 'getMaterials'
        })
    },
    beforeMount () {
        console.log('patternInfo : ', this.patternInfo)
        console.log('pattern name : ',this.$route.params.pattern)
        this.getPatternInfo(this.$route.params.pattern)
    },
    mounted() {
        //this.getMaterials()
    }
/*  components : {
    TODO: Add components
  }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  [v-cloak] {display: none}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



