<template>
  <div class="applied-filters" v-if="(appliedColorFilters.length() >= 1) || (appliedPatternFilters.length() >= 1)">
    <h2>applied filters</h2>
    <b>colors</b>
    <span v-for="appliedColorFilter in appliedColorFilters" v-bind:style="{ 'color': appliedColorFilter.colorName }">
      {{appliedColorFilter.colorName}}
    </span>
    <b>patterns</b>
    <span v-for="appliedPatternFilter in appliedPatternFilters" >
      {{appliedPatternFilter.name}}
    </span>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    /*    components : {
     WallpaperPattern
     },*/
    computed: {
      ...mapGetters({
        appliedColorFilters: 'getAppliedColorFiltersFromStore',
        appliedPatternFilters : 'getAppliedPatternFiltersFromStore'
      })
    }
  }
</script>




